@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
body, p, h1 {
  background-color: black;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  color: chartreuse;
  padding: 0;
  margin: 0;
}

#historyframe {
  width: 100%;
  margin-bottom: 50px;
}

#typeframe {
  width: 100%;
  flex-grow: 1;
  position: fixed;
  left: 0px;
  bottom: 0;
  background-color: black;
  border-top: 1px solid chartreuse;
}

#typearea {
  color: white;
  padding: 15px;
  outline: 0;
}

.mainframe {
  padding: 15px;
}

.overlay {
  background: url("../imgs/back.jpg");
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.welcome {
  border: 4px dotted chartreuse;
  background-color: rgba(0, 0, 0, 0.8);
  padding: 15px;
}
.welcome h1 {
  font-size: 18px;
  font-weight: 400;
}

.welcome-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 25px;
}

.logform {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.logform-btn {
  padding: 10px;
  background-color: chartreuse;
  color: black;
  font-family: inherit;
  font-size: 16px;
  border: 0;
}

.logform-input {
  height: 26px;
  border: 1px solid chartreuse;
  background-color: transparent;
  outline: 0;
  color: white;
  font-family: inherit;
  font-size: 16px;
}

.hidden {
  visibility: hidden;
}

